<template>
  <div>
    <v-toolbar id="app-toolbar" app dark color="pink" 
      flat
      fixed
      scroll-off-screen
      scroll-threshold="120">
      <v-toolbar-title>CornApp Admin</v-toolbar-title>
    </v-toolbar>
    <v-content>
      <list-lahan 
        v-if="currentView == 'list'" 
        v-on:active-change="handleActiveChange"/>
      <editor 
        v-if="currentView == 'editor'" 
        v-on:close="currentView = 'list'"
        :active-id="activeId"/>
    </v-content>
  </div>
</template>

<script>
import ListLahan from './ListLahan'
import Editor from './Editor'

export default {
  name: 'MainContainer',

  components: {
    ListLahan,
    Editor
  },

  data () {
    return {
      currentView: 'list',
      activeId: null
    }
  },

  methods: {
    handleActiveChange (active) {
      this.activeId = active
      this.currentView = 'editor'
    }
  }
}
</script>

<style>
#app-toolbar .v-toolbar__content {
  justify-content: center;
}
</style>



