<template>
  <v-container fluid class="pa-0 ma-0">
    <v-snackbar
      v-model="snack.deleteSucc"
      top>
      Sukses menghapus data
    </v-snackbar>

    <v-snackbar
      v-model="snack.deleteFail"
      top>
      Gagal menghapus data
    </v-snackbar>
    <v-toolbar
      color="pink"
      dark
      flat
    >
      <v-text-field
        append-icon="mic"
        class="mx-3"
        flat
        label="Search"
        prepend-inner-icon="search"
        solo-inverted
        v-model="keyword"
      ></v-text-field>
    </v-toolbar>
    <v-container grid-list-xl>
      <v-layout wrap>
        <v-flex md4 sm12 v-for="(it, index) in filteredItems" :key="index">
          <v-card>
            <v-img :src="defPic" height="260px">
              <v-container
                fill-height
                fluid
                pa-2
              >
                <v-layout fill-height>
                  <v-flex xs12 align-end flexbox>
                    <span class="headline white--text">{{ it.lokasi }}</span>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-img>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn dark icon color="blue" @click="$emit('active-change', it._id)">
                <v-icon>build</v-icon>
              </v-btn>
              <v-btn icon dark color="red" @click="remove(it._id)">
                <v-icon>clear</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import { getAllData, remove } from '../services'
const defPic = 'https://cdn.vuetifyjs.com/images/cards/house.jpg'

export default {
  data () {
    return {
      defPic,
      items: [],
      snack: {
        deleteSucc: false,
        deleteFail: false
      },
      keyword: ''
    }
  },

  methods: {
    loadItems () {
      getAllData()
        .then(items => {
          this.items = items.map(it => {
            // Provide default picture if it doesn't contain picture
            if (it.picture === undefined) {
              it.picture = defPic
            }
            return it
          })
        })
        .catch(err => {
          console.log(err)
        })
    },
    async remove (id) {
      let result = await remove(id)
      if (result.status !== 200) {
        this.snack.deleteSucc = false
        this.snack.deleteFail = true
      } else {
        this.snack.deleteSucc = true
        this.snack.deleteFail = false
      }
    }
  },

  computed: {
    filteredItems () {
      let upperIndex = this.keyword.toUpperCase()
      return this.items.filter(it => {
        let loc = it.lokasi
        return loc.toUpperCase().indexOf(upperIndex) !== -1
      })
    }
  },

  created () {
    this.loadItems()
  }
}
</script>

<style>

</style>



