import * as axios from 'axios'

const baseUrl = 'http://localhost:3000'

export function getAllData () {
  return axios.get(`${baseUrl}/api/lahan`)
    .then(resp => resp.data)
}

export function getById (id) {
  return axios.get(`${baseUrl}/api/lahan/${id}`)
    .then(resp => resp.data)
}

export function update (id, payload) {
  return axios.put(`${baseUrl}/api/lahan/${id}`, payload)
}

export function remove (id) {
  return axios.delete(`${baseUrl}/api/lahan/${id}`)
}

